html {
  box-sizing: border-box; }

*, *:before, *:after {
  box-sizing: inherit; }

body {
  margin: 0;
  padding: 0;
  font-family: -apple-system, BlinkMacSystemFont, "Segoe UI", "Roboto", "Oxygen", "Ubuntu", "Cantarell", "Fira Sans", "Droid Sans", "Helvetica Neue", sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale; }

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, "Courier New", monospace; }

.my-gallery-class {
  list-style: none; }

.img-responsive {
  border-top-left-radius: calc(0.25rem - 1px);
  border-top-right-radius: calc(0.25rem - 1px);
  margin-bottom: 1rem;
  margin-right: 0.5rem; }

img {
  height: 225px;
  width: 100%;
  display: block;
  object-fit: cover; }

.filter__container {
  margin-left: auto;
  margin-right: 15px;
  min-width: 250px; }
  @media screen and (max-width: 991.98px) {
    .filter__container {
      margin-left: 15px;
      margin-right: 15px;
      width: 100%; } }

.loader {
  border-radius: inherit;
  position: absolute;
  width: 100%;
  height: 100%;
  top: 0;
  left: 0;
  z-index: 8;
  background-color: rgba(244, 245, 247, 0.73); }
  .loader::before {
    content: '';
    position: absolute;
    width: 100%;
    height: 100%;
    z-index: 9;
    top: 0;
    left: 0;
    border-radius: 5px; }
  .loader svg {
    fill: #4ce1b6;
    position: absolute;
    animation: refresh 1s infinite linear;
    z-index: 10;
    width: 48px;
    height: 48px;
    top: calc(50% - 24px);
    left: calc(50% - 24px); }

@keyframes refresh {
  from {
    transform: rotate(0deg); }
  to {
    transform: rotate(360deg); } }

.not-found-page {
  background-color: #111;
  min-height: 100vh;
  padding: 40px;
  font-size: 75px;
  font-family: 'Monoton', cursive;
  text-align: center;
  text-transform: uppercase;
  text-shadow: 0 0 80px red, 0 0 30px FireBrick, 0 0 6px DarkRed;
  color: red; }
  .not-found-page p {
    margin: 0; }
  .not-found-page .error {
    color: #fff;
    text-shadow: 0 0 80px #fff, 0 0 30px #008000, 0 0 6px #00f; }
    .not-found-page .error span {
      animation: upper 11s linear infinite; }
    .not-found-page .error:hover {
      text-decoration: none;
      text-shadow: 0 0 200px #fff, 0 0 80px #008000, 0 0 6px #00f; }
  .not-found-page .code span:nth-of-type(1) {
    text-shadow: none;
    opacity: 0.4; }
  .not-found-page .code span:nth-of-type(2) {
    animation: lower 10s linear infinite; }
  .not-found-page .code:hover {
    text-shadow: 0 0 100px red, 0 0 40px FireBrick, 0 0 8px DarkRed; }

@keyframes upper {
  0%,
  19.999%,
  22%,
  62.999%,
  64%,
  64.999%,
  70%,
  100% {
    opacity: 0.99;
    text-shadow: 0 0 80px #fff, 0 0 30px #008000, 0 0 6px #00f; }
  20%,
  21.999%,
  63%,
  63.999%,
  65%,
  69.999% {
    opacity: 0.4;
    text-shadow: none; } }

@keyframes lower {
  0%,
  12%,
  18.999%,
  23%,
  31.999%,
  37%,
  44.999%,
  46%,
  49.999%,
  51%,
  58.999%,
  61%,
  68.999%,
  71%,
  85.999%,
  96%,
  100% {
    opacity: 0.99;
    text-shadow: 0 0 80px red, 0 0 30px FireBrick, 0 0 6px DarkRed; }
  19%,
  22.99%,
  32%,
  36.999%,
  45%,
  45.999%,
  50%,
  50.99%,
  59%,
  60.999%,
  69%,
  70.999%,
  86%,
  95.999% {
    opacity: 0.4;
    text-shadow: none; } }
